import http from "./http.js";

/**
 * 获取 `商品订单`
 *
 * @author yq <447845126@qq.com>
 * @time   2019-02-12
 * @return {[type]}   [description]
 */
export function orderList(params) {
  return http.get("/orders", {
    params
  });
}

/**
 * 发货
 *
 * @author yq <447845126@qq.com>
 * @time   2019-01-24
 * @param  {Int}      options.express_id 快递ID
 * @param  {String}   options.express_on 运单号
 */
export function sendOrder({ orderId, express_id, express_no }) {
  return http.put(`/order/${orderId}`, {
    express_id,
    express_no
  });
}

/**
 * 获取 `快递公司`
 *
 * @author yq <447845126@qq.com>
 * @time   2019-01-26
 */
export function expressList(params) {
  return http({
    method: "get",
    url: "/express",
    params
  })
}