<template>
  <div class="order">
    <el-dialog
      title="选择快递"
      :visible.sync="dialogVisible"
      width="50%"
      tooltip-effect="dark"
      border
      stripe
    >
      <el-input
        placeholder="请输入快递名称"
        v-model="searchExpress.name"
        class="input-with-select"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="searchExpresss"
        ></el-button>
      </el-input>
      <el-input
        size="mini"
        class="order__input"
        placeholder="请输入运单号"
        v-model="expressForm.express_no"
      >
        <template slot="prepend"
          >运单号 :</template
        >
      </el-input>
      <el-table
        :data="expressList"
        style="width: 100%"
        height="400"
        border
        stripe
      >
        <el-table-column prop="id" label="快递ID" width="80" align="center">
        </el-table-column>
        <el-table-column prop="name" label="快递名" width="150" align="left">
        </el-table-column>
        <el-table-column prop="code" label="快递代码"> </el-table-column>
        <el-table-column label="操作" width="100" align="center">
          <template slot-scope="{ row }">
            <el-button type="primary" @click="select(row)">发货</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChangeExpress"
        :current-page="searchExpress.page"
        :page-size="searchExpress.limit"
        layout="total, prev, pager, next, jumper"
        :total="totalExpress"
      >
      </el-pagination>
    </el-dialog>
    <el-form :inline="true">
      <el-form-item label="订单号 :">
        <el-input size="mini" v-model="search.order_no"></el-input>
      </el-form-item>
      <el-form-item label="商品名称 :">
        <el-input size="mini" v-model="search.name"></el-input>
      </el-form-item>
      <el-form-item label="日期 :">
        <el-date-picker
          size="mini"
          v-model="time"
          type="datetimerange"
          range-separator="至"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
          value-format="yyyy-MM-dd HH:mm:ss"
          @change="changeTime">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button 
          size="mini" 
          type="primary" 
          @click="searchOrder" 
          icon="el-icon-search">搜索</el-button>
      </el-form-item>
    </el-form>
    <el-tabs v-model="search.order_status" type="card" @tab-click="searchOrder">
      <el-tab-pane label="全部" name="-1"></el-tab-pane>
      <el-tab-pane label="待付款" name="0"></el-tab-pane>
      <el-tab-pane label="待发货" name="1"></el-tab-pane>
      <el-tab-pane label="已发货" name="2"></el-tab-pane>
      <el-tab-pane label="已完成" name="3"></el-tab-pane>
      <el-tab-pane label="已退款" name="4"></el-tab-pane>
      <el-tab-pane label="已退货" name="5"></el-tab-pane>
      <el-tab-pane label="已关闭" name="6"></el-tab-pane>
    </el-tabs>
    <el-table :data="orderList" style="width: 100%" border stripe height="600">
      <el-table-column
        prop="order_id"
        label="订单ID"
        width="80"
        align="center"
      ></el-table-column>
      <el-table-column prop="goods_name" label="商品信息" min-width="300">
        <template slot-scope="{ row }">
          <div class="order__goods">
            <img class="order__goods-image" :src="row.goods_image" />
            <p>{{ row.goods_name }}</p>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="order_no"
        label="订单号"
        width="200"
        align="center"
      ></el-table-column>
      <el-table-column prop="name" label="买家" width="120" align="center">
        <template slot-scope="{ row }">
          <p>{{ row.name }}</p>
          <p>{{ row.phone }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="单价(元)/数量"
        width="130"
        align="right"
      >
        <template slot-scope="{ row }">
          <p class="order__goods-money">￥{{ row.goods_price }}</p>
          <p>× {{ row.num }}</p>
        </template>
      </el-table-column>
      <el-table-column
        prop="created_at"
        label="创建时间"
        width="180"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="order_status"
        label="交易状态"
        width="120"
        align="center"
      >
        <template slot-scope="{ row }">
          <el-tag :type="row.order_status | changeTag">{{ row.order_status | changStatus }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="express_name"
        label="快递名"
        width="120"
        align="left"
      >
      </el-table-column>
      <el-table-column
        prop="express_no"
        label="运单号"
        width="300"
        align="left"
      >
      </el-table-column>
      <el-table-column prop="address" label="操作" width="120" align="center">
        <template slot-scope="{ row }">
          <el-button
            size="mini"
            :disabled="row.order_status >= 2"
            type="success"
            @click="sendOrder(row)"
            >选择快递</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="search.page"
      :page-size="search.limit"
      layout="total, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>
<script>
import { orderList, sendOrder, expressList } from "@/api/order.js";

export default {
  name: "Order",
  data() {
    return {
      expressForm: {
        orderId: 0,
        express_id: 0,
        express_no: ""
      },
      search: {
        name: "",
        start_time: "",
        end_time: "",
        order_no: "",
        order_status: "-1",
        page: 1,
        limit: 15
      },
      searchExpress: {
        name: "",
        code: "",
        limit: 15,
        page: 1
      },
      orderList: [],
      expressList: [],
      total: 0,
      totalExpress: 0,
      dialogVisible: false,
      name: "",
      time: []
    };
  },
  filters: {
    changeTag(val) {
      switch(val) {
        case 0:
          return " ";
        case 1:
          return " ";
        case 2:
          return "success";
        case 3:
          return 'success';
        case 4:
          return "danger";
        case 5:
          return "danger";
      }
    },
    changStatus(val) {
      switch (val) {
        case 0:
          return "待付款";
        case 1:
          return "待发货";
        case 2:
          return "已发货";
        case 3:
          return "已完成";
        case 4:
          return "已退款";
        case 5:
          return "已退货";
        case 6:
          return "已关闭";
      }
    }
  },
  methods: {
    changeTime(val) {
      if(val) {
        this.search.start_time = val[0];
        this.search.end_time = val[1];
      }else {
        this.search.start_time = this.search.end_time = "";
      }
    },
    select(row) {
      this.expressForm.express_id = row.id;
      if (this.expressForm.express_no.trim() === "") {
        return this.$message.error("请填写运单号!");
      }
      this.$confirm(`${this.name},确认发货?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "success"
      })
        .then(() => {
          sendOrder(this.expressForm)
            .then(() => {
              this.dialogVisible = false;
              this.expressForm.express_no = "";
              this.name = "";
              this.getOrderList();
              this.$message({
                type: "success",
                message: "发货成功!"
              });
            })
            .catch(data => {
              this.$message.error(data.response.data.message);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消发货"
          });
        });
    },
    handleCurrentChangeExpress(page) {
      this.searchExpress.page = page;
      this.getExpressList();
    },
    getExpressList() {
      expressList(this.searchExpress).then(({ data: { data, total } }) => {
        this.expressList = data;
        this.totalExpress = total;
      });
    },
    sendOrder(row) {
      this.dialogVisible = true;
      this.expressForm.orderId = row.id;
      this.name = row.goods_name;
    },
    handleCurrentChange(page) {
      this.search.page = page;
      this.getOrderList();
    },
    getOrderList() {
      orderList(this.search).then(({ data: { data, total } }) => {
        this.orderList = data;
        this.total = total;
      });
    },
    searchOrder() {
      this.search.page = 1;
      this.getOrderList();
    },
    searchExpresss() {
      this.searchExpress.page = 1;
      this.getExpressList();
    }
  },
  created() {
    this.getOrderList();
    this.getExpressList();
  }
};
</script>

<style lang="scss">
$prefix: "order";

.#{$prefix} {
  &__input {
    margin: 10px 0 ;
    width: 400px;
  }
  &__goods {
    &-image {
      float: left;
      margin-right: 10px;
      width: 120px;
      object-fit: cover;
    }
  }
  &__goods-money {
    color: #67C23A;
  }
}
</style>
